import React from "react";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";

const Summary = ({
  currency,
  value1,
  value2,
  paymentType,
  percent,
  summAnnual,
  summ
}) => {
  return (
    <div className="summary__wrap">
      <Link to="/">
        <button className="ui-btn--active summary__btn"> </button>
      </Link>
      <p className="summary__title-main"> :</p>
      <div className="summary__titles-wrap">
        <span className="summary__title">
          :
          {` ${currency}`}{" "}
        </span>
        <span className="summary__title">: {` ${value1}`}</span>
        <span className="summary__title">: {` ${value2} `}</span>
        <span className="summary__title">
           :{" "}
          {paymentType === "month" ? "" : "  "}
        </span>
        <span className="summary__title">
           : {` ${percent} %`}
        </span>
      </div>
      <div className="result__wrap">
        <h1 className="result__title">   </h1>
        <div className="result__numbers-wrap">
          <div className="result__annual-wrap">
            <span className="result__annual-number">
              {Math.ceil(summAnnual)} {currency === "USD" ? "$" : "."}
            </span>
            <span className="result__annual-caption">
              {" "}
                 {" "}
            </span>
          </div>

          <div className="result__month-wrap">
            <span className="result__month-number">
              {Math.ceil(summ)}
              {currency === "USD" ? "$" : "."}
            </span>
            <span className="result__month-caption">  </span>
          </div>
        </div>

        <button className="result__btn"></button>
      </div>
    </div>
  );
};

export default Summary;



